.App {
    max-width: 1250px;
    margin: 0 auto;
    padding: 0 20px;
}

.schedule {
    columns: 300px auto;
    margin-top: 1em;
}

.schedule__item {
    padding: 6px;
    border: 1px solid #eee;
    border-radius: 5px;
}

.schedule__item:first-child {
    margin-top: 0;
}

.event-count {
    display: flex;
}

.event-count__event {
    margin-right: 2em;
}

.config__item {
    width: 100%;
    display: flex;
}

.config__item input[type=range] {
    flex: 1;
}
